import React from "react";
import Axios from "axios";
import { useState } from "react";
import "./Navbar.scss";
import useAuth from "../hooks/useAuth";
import logoAkun from "../images/woman.png";
import logoPuskesmas from "../images/image 3.png";

function Navbar() {
  const { setAuth } = useAuth();
  const [editPanggil, setEditPanggil] = useState(false);
  const [editVerif, setEditVerif] = useState(false);
  const [editJam, setEditJam] = useState(false);
  const [templateTerima, setTerima] = useState("");
  const [templateVerif, setVerif] = useState("");
  const [waktuMulai, setMulai] = useState("");
  const [waktuSelesai, setSelesai] = useState("");
  const BASE_URL = "https://capilaonline.site/api";

  const Pengaturan = async () => {
    try {
      await Axios.get(
        BASE_URL + "/pesan"
        // await Axios.delete("http://localhost:8080/api/auth/logout", {
      ).then((response) => {
        setTerima(response.data[0].panggil_pesan);
        setVerif(response.data[0].verif_pesan);
        setMulai(response.data[0].jam_mulai);
        setSelesai(response.data[0].jam_selesai);
      });
    } catch (error) {
      console.log(error);
    }
  };

  const Logout = async () => {
    try {
      await Axios.delete(BASE_URL + "/logout", {
        // await Axios.delete("http://localhost:8080/api/auth/logout", {
        withCredentials: true,
      }).then(() => {
        setAuth({});
      });
    } catch (error) {
      console.log(error);
    }
  };

  const setPesanTerima = async () => {
    try {
      await Axios.patch(
        BASE_URL + "/pesan/1", {
        panggil_pesan: templateTerima,
      });
      setEditPanggil(false);
    } catch (error) {
      console.log(error);
    }
  };

  const setPesanVerif = async () => {
    try {
      await Axios.patch(BASE_URL + "/pesan/1", {
        verif_pesan: templateVerif,
      });
      setEditVerif(false);
    } catch (error) {
      console.log(error);
    }
  };

  const setJamOperasional = async () => {
    try {
      await Axios.patch(BASE_URL + "/pesan/1", {
        jam_mulai: waktuMulai,
        jam_selesai: waktuSelesai,
      });
      setEditJam(false);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-light py-3">
      <div className="container-fluid">
        <div className="collapse navbar-collapse">
          <ul className="navbar-nav me-lg-auto ps-3">
            <img className="" src={logoPuskesmas} width="50" alt="" />
          </ul>
        </div>

        <span className="navbar-brand mx-lg-auto d-flex align-items-center gap-1">
          <img
            className="d-lg-none d-none d-sm-inline-block"
            src={logoPuskesmas}
            width="40"
            alt=""
          />
          <span className="title fw-bold">Puskesmas Pattingalloang</span>
        </span>

        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-lg-auto d-flex align-items-center pe-3">
            <div className="dropdown d-none d-lg-inline-block">
              <a
                href="/"
                className="d-flex align-items-center text-dark text-decoration-none dropdown-toggle"
                id="dropdownUser1"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <img className="" src={logoAkun} width="50" alt="" />
              </a>
              <ul
                className="dropdown-menu dropdown-menu-end dropdown-menu-light text-small shadow text-center mt-lg-3 pb-0"
                aria-labelledby="dropdownUser1"
              >
                <div className="px-5 pt-3">
                  <li>
                    <img className="" src={logoAkun} width="60" alt="" />
                  </li>
                  <li className="mt-2 mb-1">
                    <span className="fw-bold d-block">Admin</span>
                  </li>
                </div>
                <li>
                  <button
                    className="dropdown-item logout-large"
                    data-bs-toggle="modal"
                    data-bs-target="#modalPengaturan"
                  >
                    Pengaturan
                  </button>
                </li>
                <li>
                  <button
                    onClick={Logout}
                    className="dropdown-item logout-large text-danger pb-2"
                  >
                    Log out
                  </button>
                </li>
              </ul>
            </div>
            <ul className="navbar-nav d-inline-block text-center d-lg-none">
              <li>
                <button
                  className="navbar-btn pengaturan-btn"
                  data-bs-toggle="modal"
                  data-bs-target="#modalPengaturan"
                  onClick={Pengaturan}
                >
                  Pengaturan
                </button>
              </li>
              <li>
                <button onClick={Logout} className="navbar-btn logout-btn">
                  Log out
                </button>
              </li>
            </ul>
          </ul>
        </div>
        <div
          className="modal fade"
          id="modalPengaturan"
          aria-hidden="true"
          aria-labelledby="modalPengaturanToggleLabel"
          tabindex="-1"
        >
          <div className="modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-header">
                <h5
                  className="modal-title fw-bold"
                  id="modalPengaturanToggleLabel"
                >
                  PENGATURAN
                </h5>
                <button
                  type="button"
                  class="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div className="modal-body data-pasien">
                <div className="mb-2">
                  <div className="mb-1 d-flex align-items-center justify-content-between">
                    <span>Template Pesan WA Terima</span>
                    <div>
                      <button
                        className="btn-edit p-0"
                        onClick={() => setEditPanggil(true)}
                      >
                        <i className="fa-solid fa-pen-to-square text-danger fs-5"></i>
                      </button>
                      {editPanggil && (
                        <button
                          className="btn-edit p-0 ms-3"
                          onClick={setPesanTerima}
                        >
                          <i class="fa-solid fa-check text-success fs-5"></i>
                        </button>
                      )}
                    </div>
                  </div>
                  <textarea
                    className="w-100"
                    name=""
                    id=""
                    rows="3"
                    value={templateTerima}
                    onChange={(e) => setTerima(e.target.value)}
                    disabled={!editPanggil && "disabled"}
                  ></textarea>
                  <span className="note-font">
                    * [[jadwal]] dan [[no.antrean]] akan disesuaikan dengan
                    antrean
                  </span>
                </div>
                <div className="mb-2">
                  <div className="mb-1 d-flex align-items-center justify-content-between">
                    <span>Template Pesan WA Verif Ulang</span>
                    <div>
                      <button
                        className="btn-edit p-0"
                        onClick={() => setEditVerif(true)}
                      >
                        <i className="fa-solid fa-pen-to-square text-danger fs-5"></i>
                      </button>
                      {editVerif && (
                        <button
                          className="btn-edit p-0 ms-3"
                          onClick={setPesanVerif}
                        >
                          <i class="fa-solid fa-check text-success fs-5"></i>
                        </button>
                      )}
                    </div>
                  </div>
                  <textarea
                    className="w-100"
                    name=""
                    id=""
                    rows="3"
                    value={templateVerif}
                    onChange={(e) => setVerif(e.target.value)}
                    disabled={!editVerif && "disabled"}
                  ></textarea>
                  <span className="note-font">
                    * [[alasan]] akan disesuaikan dengan antrean
                  </span>
                </div>
                <div>
                  <div className="mb-1 d-flex align-items-center justify-content-between">
                    <span>Atur Jam Operasional</span>
                    <div>
                      <button
                        className="btn-edit p-0"
                        onClick={() => setEditJam(true)}
                      >
                        <i className="fa-solid fa-pen-to-square text-danger fs-5"></i>
                      </button>
                      {editJam && (
                        <button
                          className="btn-edit p-0 ms-3"
                          onClick={setJamOperasional}
                        >
                          <i class="fa-solid fa-check text-success fs-5"></i>
                        </button>
                      )}
                    </div>
                  </div>
                  <div className="w-100">
                    <input
                      className="w-25"
                      type="time"
                      value={waktuMulai}
                      onChange={(e) => setMulai(e.target.value)}
                      disabled={!editJam && "disabled"}
                    />
                    <span> - </span>
                    <input
                      className="w-25"
                      type="time"
                      value={waktuSelesai}
                      onChange={(e) => setSelesai(e.target.value)}
                      disabled={!editJam && "disabled"}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
